@import url(https://fonts.googleapis.com/css2?family=Chela+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Chela+One&family=Oswald:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background: rgb(251,255,208);
  background: linear-gradient(169deg, rgba(251,255,208,1) 0%, rgba(240,173,120,0.9850315126050421) 95%);
  min-height: 900px;
  padding: 20px;
  font-family: 'Chela One', cursive;
}

.mainImg {
  border-radius: 50%;
  width: 270px;
  height: 150px;
  margin-right: 50px;
  border: 3px solid rgb(180, 148, 105);;
}
.App h1 {
  font-size: 4.5rem;
  color: rgb(180, 148, 105);
  margin-right: 200px;
}
.btnNew {
  background-color: rgb(243, 205, 122);
  width: 100px;
  height: 40px;
  border-radius: 30px;
  border-color: darkkhaki;
  margin-right: 3px;
}
.btnService {
  background-color: rgb(231, 104, 20);
  width: 100px;
  height: 40px;
  border-radius: 30px;
  border-color: darkkhaki;
}
table, th, td {
    margin-top: 50px;
    border: 2px solid black;
